/* Import Bootstrap */
@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css');

/* Background with Gradient and Blur Effect */
.search-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(135deg, #a8edea 0%, #fed6e3 100%);
  overflow: hidden;
  position: relative;
}

.search-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  z-index: -1;
}

/* Filter Section */
.filter-section {
  background: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.filter-section h2 {
  color: #2c3e50;
  font-size: 1.5rem;
  margin-bottom: 20px;
}

/* Filter Inputs */
.filter-input {
  background: rgba(255, 255, 255, 0.9);
  color: #2c3e50;
  border: 1px solid #ced4da;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 10px;
  width: 100%;
}

.filter-input::placeholder {
  color: #6c757d;
}

/* Filter Buttons */
.filter-btn {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 10px;
  font-weight: bold;
  transition: background 0.3s ease-in-out;
  background: #3498db;
  color: #ffffff;
  border: none;
}

.filter-btn:hover {
  background: #2980b9;
}

/* Dog Cards Section */
.dog-section {
  padding: 20px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
}

/* Mobile Filter Button */
.mobile-filter-btn {
  display: none;
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 1000;
  background: #3498db;
  color: #ffffff;
  border: none;
  padding: 10px;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 30px;
  font-size: 20px;
}

/* Sticky Next/Previous Buttons */
.sticky-buttons {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  z-index: 1000;
  background: rgba(255, 255, 255, 0.8);
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.pagination-btn {
  background: #3498db;
  color: #ffffff;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  transition: background 0.3s ease-in-out;
}

.pagination-btn:hover {
  background: #2980b9;
}

.pagination-btn:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
}

.page-number {
  display: flex;
  align-items: center;
  font-weight: bold;
  color: #2c3e50;
}

/* Responsive Design */
@media (max-width: 768px) {
  .search-container {
    padding: 10px;
  }

  .mobile-filter-btn {
    display: block;
  }

  .filter-section {
    display: none;
    width: 100%;
    margin-bottom: 20px;
    margin-right: 0;
  }

  .filter-section.open {
    display: block;
  }

  .dog-section {
    width: 100%;
  }
}